.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}

.input {
    border-radius: 15px;
    width: 220px;
    height: 35px;
    margin-bottom: 15px;
    background-color: khaki;
    outline: none;
    font-size: 14px;
    text-align: center;
    border: none;
}

.form_button {
    height: 26px;
    width: 160px;
    border-radius: 15px;
    background-color: khaki;
}
.form_button:hover {
    cursor: pointer;
    background-color: yellow;
}

.data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid ;
    border-radius: 15px;
    padding: 15px;
    margin-bottom: 20px;
}

.description {
    display: flex;
    flex-direction: column;
}

.back {
    display: flex;
    justify-content: center;
    align-items: center;
}

.back:hover {
    cursor: pointer
}

